.expense-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: black;
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    justify-content: center;
  }
  
  .expense-item h2 {
    font-size: 1rem;
    margin: 0 1rem;
    color: white;
  }
  .headding-title{
    font-size: 1.3rem;
    margin: 0 1rem;
    color: rgb(220, 220, 220, 0.5);
    display: block;
  }
  .headding-amount{
    font-size: 1.3rem;
    margin: 0 1rem;
    color: rgba(255, 0, 0, 0.5);
    /* display: block; */
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: transparent;
    /* border: 1px solid white; */
    padding: 0.5rem;
    border-radius: 12px;
  }
  
  .heading{

  }

  @media (min-width: 580px) {
    .expense-item__description {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  
    .expense-item__description h2 {
      font-size: 1.25rem;
    }
  
    .expense-item__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
    }
  }