.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4.6rem;
    /* margin-bottom: 1rem; */
    text-align: left;
  }
  
  .new-expense__control label {
    margin-bottom: 0.5rem;
    display: block;
    color: #ccc;
    text-align: center;
    font-size: 12px;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 12rem;
    max-width: 100%;
  }
  
  .new-expense__actions {
    width: 100%;
    display: flex;
    justify-content: end;
    gap: 2;
  }
  .btn-cnl{
    margin-right: 8px;
  }